@import url(../fonts/fontsOfNightTrips/stylesheet.css);

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul,
li {
  list-style: none;
}

a,
a:hover {
  text-decoration: none;
}

input,
button,
select,
textarea {
  outline: none !important;
  border: none !important;
}

body {
  background: #fff;
  font-family: 'Larsseit', sans-serif;
  font-weight: 400;
}

.container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 100px;
}

.row {
  width: 100%;
  flex-wrap: wrap;
  flex-shrink: 0;
}

.logo {
  font-family: "Playfair";
  font-weight: 700;
  font-size: 36px;
  line-height: 190%;
  color: #fff;
  text-transform: uppercase;
}

.logo span {
  color: #41EAD4;
}

.title {
  font-family: "Noe";
  font-weight: 700;
  font-size: 72px;
  line-height: 79px;
  letter-spacing: -0.02em;
  color: #fff;
  text-transform: inherit;
}

.btn {
  box-shadow: 5px 15px 40px rgba(0, 201, 224, 0.2);
  border-radius: 48px;
  width: 167px;
  height: 62px;
  cursor: pointer;
  position: relative;
  color: #fff;
  text-transform: uppercase;
  background: #00C9E0;
}

.btn::after {
  content: "";
  height: 2px;
  width: 30px;
  border-radius: 16px;
  background: #fff;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translate(50%, -50%);
}

.h-line {
  width: 2px;
  height: 80px;
  background: linear-gradient(180deg, #09F4F4 0%, #00C9E0 100%);
  border-radius: 13px;
}

.gradient {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 377px;
  width: 100%;
  background: linear-gradient(180deg, rgba(1, 22, 39, 0) 0%, #011627 76.64%, #011627 100%);
  z-index: 1;
}

.header {
  position: relative;
  background: url("../img-original/header_bg.jpg") no-repeat center center/cover;
  padding: 77px 0 234px;
}

.header__container {
  max-width: 1650px;
}

.header-nav.active {
  position: fixed;
  z-index: 4;
  width: 100%;
  height: 100%;
  padding: 77px 100px;
  top: 0;
  left: 0;
  background: #02223b;
}

.header-nav.active .header-nav-menu {
  margin-bottom: 50px;
}

.header-nav.active .header-nav-hidden {
  height: 300px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
}

.header-nav.active .header-nav-hidden .header-nav-hidden__link {
  font-size: 25px;
  color: #fff;
  text-transform: uppercase;
}

.header-nav-hidden {
  display: none;
}

.header-nav-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 156px;
}

.header-nav-menu__link:first-child {
  flex: 1;
}

.header-nav-menu__link:nth-child(3) {
  margin-left: 35px;
}

.header-nav-menu__btn_close {
  display: none;
  cursor: pointer;
  border: none;
  background: none;
  width: 30px;
  height: 16px;
  transform: rotate(0);
  position: relative;
}

.header-nav-menu__btn_close span {
  transition: .3s;
  position: absolute;
  width: 100%;
  left: 0;
  background: #fff;
  height: 3px;
  transform: rotate(0);
}

.header-nav-menu__btn_close span:nth-child(1) {
  top: 0;
}

.header-nav-menu__btn_close span:nth-child(2),
.header-nav-menu__btn_close span:nth-child(3) {
  top: 8px;
}

.header-nav-menu__btn_close span:nth-child(4) {
  top: 16px;
}

.header-nav-menu__btn_close.open span:nth-child(1),
.header-nav-menu__btn_close.open span:nth-child(4) {
  left: 50%;
  width: 0;
  top: 8px;
}

.header-nav-menu__btn_close.open span:nth-child(2) {
  transform: rotate(45deg);
}

.header-nav-menu__btn_close.open span:nth-child(3) {
  transform: rotate(-45deg);
}

.header__title {
  font-family: "Noe";
  font-weight: 700;
  font-size: 120px;
  line-height: 153px;
  letter-spacing: -0.02em;
  color: #fff;
  text-align: center;
  text-transform: capitalize;
  margin-bottom: 14px;
}

.header__subT {
  font-size: 18px;
  line-height: 24px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #fff;
  text-align: center;
  margin-bottom: 50px;
  font-weight: 700;
}

.header__txt {
  font-size: 17px;
  line-height: 25px;
  text-align: center;
  color: #fff;
  max-width: 643px;
  margin: 0 auto 192px;
}

.header-bottom {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.header-bottom-scroll {
  z-index: 2;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.header-bottom-scroll__txt {
  font-size: 15px;
  line-height: 28px;
  letter-spacing: 0.05em;
  color: #fff;
  opacity: 0.9;
  transform: rotate(-90deg);
  text-transform: uppercase;
  margin-bottom: 45px;
}

.header-bottom-social {
  z-index: 2;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.header-bottom-social__link:nth-child(2) {
  margin: 25px 0;
}

.sections {
  margin-top: -31px;
  padding-top: 31px;
  position: relative;
  height: 4245px;
  background: #011627;
}

.sections-top {
  margin: 0 auto 83px;
  display: flex;
  justify-content: start;
  align-items: center;
  position: relative;
  z-index: 3;
}

.sections-top__btn {
  margin: 0 39px;
}

.sections-top__txt {
  max-width: 349px;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.02em;
  color: #fff;
}

.sections-nav {
  margin-bottom: 150px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  z-index: 3;
}

.sections-nav::after {
  content: "";
  position: absolute;
  bottom: 0;
  height: 2px;
  width: 100%;
  background: #41EAD4;
  opacity: 0.3;
  border-radius: 13px;
}

.sections-nav__link {
  text-align: center;
  display: inline-block;
  width: 25%;
  height: 55px;
  padding-bottom: 35px;
  position: relative;
  font-weight: 700;
  font-size: 16px;
  line-height: 18px;
  letter-spacing: -0.02em;
  color: #fff;
  opacity: 0.5;
}

.sections-nav__link.active {
  font-size: 18px;
  line-height: 20px;
  color: #41EAD4;
}

.sections-nav__link.active::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 4px;
  background: #41EAD4;
  border-radius: 13px;
}

.sections-chill {
  display: none;
  position: relative;
  z-index: 3;
  max-width: 1200px;
  margin: 0 auto;
}

.sections-chill.active {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.sections-chill-wrapper {
  width: 486px;
  height: 607px;
  position: relative;
  margin-right: 126px;
}

.sections-chill-wrapper-view {
  display: none;
  width: 100%;
  height: 100%;
}

.sections-chill-wrapper-view.active {
  display: block;
}

.sections-chill-wrapper-view-title {
  position: absolute;
  bottom: 287px;
  left: -95px;
}

.sections-chill-wrapper-view-title__num {
  font-weight: 700;
  font-size: 24px;
  line-height: 36px;
  letter-spacing: 0.15em;
  color: #41EAD4;
}

.sections-chill-wrapper-view-title__txt {
  position: absolute;
  width: 207px;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 24px;
  line-height: 36px;
  letter-spacing: 0.15em;
  color: #fff;
  z-index: 2;
}

.sections-chill-wrapper-view-title__txt::before {
  content: "";
  position: absolute;
  width: 172px;
  height: 2px;
  background: #41EAD4;
  border-radius: 13px;
  top: -54px;
}

.sections-chill-wrapper-view-title__txt::after {
  content: "";
  position: absolute;
  width: 2px;
  height: 12px;
  top: -53px;
  left: 0;
  background: #41EAD4;
  border-radius: 13px;
}

.sections-chill-wrapper-view__img {
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 1;
}

.sections-chill-wrapper-view__dots {
  position: absolute;
  bottom: -55px;
  left: -55px;
}

.sections-chill-wrapper-view-arrows {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: 2;
}

.sections-chill-wrapper-view-arrows__arrow {
  transition: .5s;
  width: 65px;
  height: 65px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #011627;
  box-shadow: -10px 14px 24px rgba(1, 22, 39, 0.08);
  cursor: pointer;
}

.sections-chill-wrapper-view-arrows__arrow:hover {
  background: #fff;
}

.sections-chill-wrapper-view-arrows__arrow:hover svg {
  background-color: #011627;
}

.sections-chill-info {
  position: relative;
  width: 650px;
  margin-right: -58px;
  padding: 67px 0 138px 0;
}

.sections-chill-info .title {
  margin-bottom: 34px;
}

.sections-chill-info p {
  font-weight: 400;
  font-size: 17px;
  line-height: 25px;
  letter-spacing: -0.02em;
  color: #fff;
  max-width: 485px;
}

.sections-chill-info__subtitle {
  font-weight: 700 !important;
  margin-bottom: 30px;
}

.sections-chill-info .btn {
  width: 282px;
  height: 65px;
  border-radius: 183px;
  margin: 45px 0 0 0;
}

.sections-chill-info-social {
  position: absolute;
  bottom: 0;
  right: 0;
}

.sections-chill-info-social__icons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 133px;
  padding: 0 15px 20px 0;
  border-bottom: 2px solid #41EAD4;
  position: relative;
  height: 40px;
}

.sections-chill-info-social__icons::before {
  content: "";
  position: absolute;
  right: 0;
  bottom: -1px;
  width: 2px;
  height: 31px;
  background: #41EAD4;
  border-radius: 13px;
}

.sections-chill-info-social__txt {
  font-weight: 700;
  text-transform: uppercase;
  font-size: 15px;
  line-height: 22px;
  letter-spacing: 0.15em;
  color: #fff;
  margin-bottom: 20px;
}

.sections-about {
  padding-top: 300px;
  position: relative;
  z-index: 3;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  align-content: center;
  margin-bottom: 275px;
}

.sections-about-info {
  padding-top: 157px;
}

.sections-about-info .title {
  text-transform: capitalize;
}

.sections-about-info p {
  max-width: 321px;
  font-size: 18px;
  line-height: 27px;
  letter-spacing: -0.02em;
  color: #fff;
}

.sections-about-info__subtitle {
  font-weight: 700;
  margin: 40px 0;
}

.sections-about-info__txt {
  font-weight: 300;
}

.sections-about-info__txt_1 {
  margin-bottom: 40px;
}

.sections-about-info .btn {
  width: 282px;
  height: 65px;
}

.sections-about-view {
  max-width: 790px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  position: relative;
}

.sections-about-view-card {
  width: 50%;
  position: relative;
}

.sections-about-view-card:hover .sections-about-view-card__owner {
  display: flex;
  justify-content: center;
  align-items: center;
}

.sections-about-view-card:hover img {
  opacity: 1;
}

.sections-about-view-card__img {
  width: 100%;
  opacity: .7;
  display: block;
}

.sections-about-view-card__owner {
  transition: .4s;
  position: absolute;
  left: -50px;
  bottom: 50px;
  width: 186px;
  height: 75px;
  background: #fff;
  box-shadow: 5px 15px 40px rgba(0, 201, 224, 0.2);
  border-radius: 8px;
  display: none;
}

.sections-about-view-card__txt {
  font-weight: 700;
  font-size: 15px;
  line-height: 22px;
  letter-spacing: 0.05em;
  color: #000;
  text-transform: uppercase;
}

.sections-about-view-card__txt span {
  color: #00C9E0;
}

.sections-about-view__dots {
  position: absolute;
  right: -80px;
  top: 50%;
  transform: translateY(-50%);
}

.sections-thoughts {
  z-index: 3;
  background: #011627;
  max-width: 1680px;
  margin: 0 auto;
  position: relative;
  padding: 100px 0 0;
  text-align: center;
}

.sections-thoughts .h-line {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -50%);
}

.sections-thoughts__subtitle {
  font-weight: 700;
  font-size: 24px;
  line-height: 36px;
  letter-spacing: 0.15em;
  color: #fff;
  margin: 31px 0 73px;
  text-transform: uppercase;
}

.sections-thoughts-comments {
  max-width: 1044px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin: 0 auto 126px;
}

.sections-thoughts-comments-card {
  background: #02223B;
  max-width: 510px;
  padding: 75px 74px 75px 102px;
  margin-bottom: 24px;
  box-shadow: 0px 20px 30px rgba(0, 201, 224, 0.03);
  border-radius: 5px;
}

.sections-thoughts-comments-card__title,
.sections-thoughts-comments-card__txt {
  font-weight: 700;
  font-size: 18px;
  line-height: 34px;
  color: #fff;
  text-align: left;
}

.sections-thoughts-comments-card__txt {
  font-weight: 300;
  margin: 25px 0;
}

.sections-thoughts-comments-card-user {
  display: flex;
  justify-content: start;
  align-items: center;
  text-align: left;
}

.sections-thoughts-comments-card-user__img {
  margin-right: 25px;
  border-radius: 5px;
}

.sections-thoughts-comments-card-user__name {
  font-weight: 700;
  font-size: 12px;
  line-height: 20px;
  letter-spacing: 0.15em;
  text-transform: uppercase;
  color: #fff;
  text-align: left;
}

.sections-bottom__title {
  position: inherit;
  z-index: 3;
  text-align: center;
  margin-bottom: 31px;
}

.sections-bottom__subtitle {
  position: inherit;
  z-index: 3;
  max-width: 388px;
  margin: 0 auto 108px;
  font-weight: 700;
  font-size: 24px;
  line-height: 36px;
  text-align: center;
  letter-spacing: 0.07em;
  color: #fff;
}

.sections-top__bg {
  background: url("../img-original/first-section_bg.png") no-repeat center center/cover;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
}

.sections-middle__bg {
  background: url("../img-original/second-section_bg.png") no-repeat center center/cover;
  position: absolute;
  top: 1170px;
  left: 0;
  width: 100%;
  height: 70%;
  z-index: 1;
}

.sections-bottom__bg {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2200px;
  background: linear-gradient(180deg, rgba(1, 22, 39, 0) 0%, #011627 76.64%, #011627 100%);
  z-index: 2;
}

.footer {
  z-index: 3;
  padding: 120px 0 150px;
  background: #011627;
  position: relative;
  border: 1px solid #0A0A0B;
  text-align: center;
}

.footer .h-line {
  position: absolute;
  left: 50%;
  top: -56px;
  transform: translateX(-50%);
}

.footer__row {
  display: flex;
  justify-content: space-evenly;
  align-items: flex-start;
  margin-bottom: 70px;
}

.footer-contact {
  text-align: left;
}

.footer-contact:nth-child(4) .footer-contact__link:first-child {
  text-transform: lowercase;
}

.footer-contact__title {
  font-weight: 700;
  font-size: 18px;
  line-height: 24px;
  text-transform: uppercase;
  color: #fff;
  position: relative;
  margin-bottom: 46px;
}

.footer-contact__title::before {
  content: "";
  position: absolute;
  height: 3px;
  width: 50px;
  bottom: -22px;
  left: 0;
  border-radius: 50px;
  background: #41EAD4;
}

.footer-contact__title::after {
  content: "";
  position: absolute;
  bottom: -22px;
  left: 53px;
  height: 5px;
  width: 5px;
  border: 1px solid #41EAD4;
  transform: rotate(45deg) translateY(50%);
}

.footer-contact__link {
  font-size: 15px;
  line-height: 40px;
  color: #fff;
  text-transform: uppercase;
}

.footer-contact-social-links a:nth-child(2) {
  margin: 0 25px;
}

.footer__copy {
  font-size: 15px;
  line-height: 25px;
  color: #fff;
  margin-bottom: 26px;
}

@media (max-width: 1310px) {
  .sections {
    height: 4900px;
  }
}

@media (max-width: 1199.98px) {
  .title {
    font-size: 58px;
  }

  .sections {
    height: 5300px;
  }

  .sections-chill {
    flex-direction: column;
  }

  .sections-chill-wrapper {
    margin: 0 auto 50px;
    width: inherit;
    height: inherit;
  }

  .sections-chill-info {
    margin: 0 auto;
    width: inherit;
  }

  .sections-chill-info-social {
    left: 55%;
    transform: translateX(-50%);
  }

  .sections-about {
    padding-top: 0;
  }

  .sections-about-info {
    margin: 0 auto 50px;
  }

  .sections-about-info p {
    max-width: 450px;
  }

  .sections-about-view {
    margin: 0 auto;
  }
}

@media (max-width: 1019px) {
  .sections {
    height: 6350px;
  }

  .sections-thoughts-comments-card {
    margin: 0 auto 24px;
  }
}

@media (max-width: 991.98px) {
  .title {
    font-size: 40px;
  }

  .header-nav-menu {
    margin-bottom: 50px;
  }

  .header-nav-menu__link:nth-child(2),
  .header-nav-menu__link:nth-child(3) {
    display: none;
  }

  .header-nav-menu__btn_close {
    display: block;
  }

  .header__title {
    font-size: 75px;
  }

  .sections-top .title {
    font-size: 34px;
  }

  .sections-top__btn {
    margin: 10px;
  }

  .sections-top__txt {
    max-width: 255px;
    font-size: 14px;
    line-height: 20px;
  }

  .footer__row {
    margin-bottom: 20px;
  }

  .footer-contact {
    width: 25%;
    margin-bottom: 50px;
  }
}

@media (max-width: 919px) {
  .sections {
    height: 6250px;
  }
}

@media (max-width: 767.98px) {
  .container {
    padding: 40px;
  }

  .header {
    padding-top: 0;
  }

  .header__container {
    padding: 60px;
  }

  .header-nav.active {
    padding: 60px;
  }

  .sections {
    height: 7759px;
  }

  .sections {
    height: 7860px;
  }

  .sections__container {
    padding: 0 40px;
  }

  .sections-chill-wrapper {
    margin-top: 100px;
  }

  .sections-chill-wrapper-view-title {
    top: -150px;
    left: 25%;
    transform: translateX(-50%);
  }

  .sections-about {
    margin-bottom: 72px;
  }

  .sections-about-view-card {
    width: 100%;
  }

  .sections-about-view-card__img {
    margin-bottom: 20px;
  }

  .sections-about-view-card__owner {
    bottom: -5px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 3;
  }

  .sections-about-view__dots {
    display: none;
  }

  .sections-thoughts .h-line {
    transform: rotate(-90deg);
  }

  .sections-thoughts-comments-card {
    padding: 40px;
  }

  .footer .h-line {
    top: -41px;
  }
}

@media (max-width: 575.98px) {
  .container {
    padding: 30px 20px;
  }

  .header {
    padding: 0;
  }

  .header-nav.active {
    padding: 30px 20px;
  }

  .header-nav.active .header-nav-hidden {
    height: 200px;
  }

  .header-nav.active .header-nav-hidden .header-nav-hidden__link {
    font-size: 18px;
  }

  .header__title {
    font-size: 55px;
    line-height: 50px;
  }

  .header__subT {
    font-size: 16px;
    margin: 20px 30px;
  }

  .header__txt {
    font-size: 15px;
    line-height: 20px;
    margin-bottom: 100px;
  }

  .sections {
    height: 6710px;
  }

  .sections-top {
    flex-direction: column;
    margin-bottom: 50px;
  }

  .sections-top .title {
    line-height: 50px;
  }

  .sections-nav__link {
    font-size: 14px;
  }

  .sections-nav__link.active {
    font-size: 16px;
  }

  .sections-chill-info p {
    font-size: 16px;
  }

  .sections-about-info .title {
    text-align: center;
  }

  .sections-thoughts .title {
    font-size: 35px;
  }

  .sections-thoughts__subtitle {
    font-size: 18px;
    line-height: 30px;
    margin-bottom: 40px;
  }

  .sections-thoughts-comments {
    margin-bottom: 40px;
  }

  .sections-thoughts-comments-card {
    margin: 15px 30px;
    padding: 15px;
  }

  .sections-thoughts-comments-card__title,
  .sections-thoughts-comments-card__txt {
    font-size: 14px;
    line-height: 30px;
  }

  .sections-bottom__subtitle {
    font-size: 18px;
    max-width: 250px;
  }

  .footer {
    padding-bottom: 40px;
  }

  .footer-contact {
    width: 100%;
    text-align: center;
  }

  .footer-contact__title::before {
    left: 50%;
