    transform: translateX(-50%);
  }

  .footer-contact__title::after {
    display: none;
  }
}

@media (max-width: 432px) {
  .sections {
    height: 6300px;
  }
}

@media (max-width: 400px) {
  .sections {
    height: 6190px;
  }
}

@media (max-width: 322px) {
  .btn::after {
    top: 65px;
    left: 50%;
    transform: translateX(-50%) rotate(-90deg);
  }

  .sections {
    height: 6010px;
  }
